@charset "UTF-8";

@font-face {
  font-family: "streamline-24px";
  src:url("./resources/streamline-24px.eot");
  src:url("./resources/streamline-24px.eot?#iefix") format("embedded-opentype"),
    url("./resources/streamline-24px.woff") format("woff"),
    url("./resources/streamline-24px.ttf") format("truetype"),
    url("./resources/streamline-24px.svg#streamline-24px") format("svg");
  font-weight: normal;
  font-style: normal;

}

.streamline-icon{
  font-family: "streamline-24px" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[data-icon]:before {
   @extend .streamline-icon;
  content: attr(data-icon);
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  @extend .streamline-icon;
}

.map-icon{
    font-size: 25px;
    color: #000;
    &:before{
      top:2px;
      position:absolute;
      left:0px;
    }
}

